import React, {useEffect, useState} from 'react';
import {FiImage} from 'react-icons/fi';
import api from '../../../dataAccessLayer/firebase'
import {storage} from "../../../dataAccessLayer/fbConfig";
import s from "../create/Create.module.scss";
import {useParams, useHistory} from 'react-router-dom';
import {Tooltips} from "../../common/Tooltips/Tooltips";


export const Edit = () => {
    const {id} = useParams()
    const history = useHistory();
    const [newItem, setNewItem] = useState({})
    const [images, setImages] = useState([])
    const [progressBar, setProgressBar] = useState(0)
    const options = ['1', '2', '3', '4', '5', '6  ']
    const [selectedRooms, setSelectedRooms] = useState(null)
    const editCondition = newItem.address !== '' && newItem.price !== ''

    const fetchEdit = async () => {
        const item = await api.getAdvById(id)
        setNewItem({...item})
        setImages([...item.imgUrl])
        setSelectedRooms(item.rooms)
    }

    useEffect(() => {
        fetchEdit()
    }, [])


    const edit = () => {
        const date = new Date()
        let priceHistory = [...newItem.priceHistory, {date: date, price: newItem.price}]
        api.updateAdv(
            newItem.id,
            images,
            newItem.address,
            newItem.price,
            priceHistory,
            newItem.phone,
            selectedRooms,
            newItem.square,
            newItem.floor,
            newItem.description,
            newItem.date,
            newItem.userId
        )
        history.push("/flat");
    }

    const closeEdit = () => {
        setImages([]);
        setProgressBar(0);
        history.push("/flat");
    }


    const onHandleUpdate = e => {
        setNewItem({...newItem, [e.target.id]: e.target.value});
    };

    //       Firebase
    const onHandleChangeFile = async e => {

        if (e.target.files !== []) {
            let aditionalArray = [];
            for (let i = 0; i < e.target.files.length; i++) {
                const file = e.target.files[i]
                if (!file.type.match('image.*')) {
                    continue
                }
                storage.ref(`/images/${file.name}`).put(file)
                    .on('state_changed',
                        async snapshot => {
                            await setProgressBar(Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100));
                            console.log('  ' + progressBar)
                        },
                        error => {
                            console.log(error)
                        },
                        async () => {
                            let url = await storage.ref('images').child(file.name).getDownloadURL()
                            aditionalArray.push(url)
                            console.log(images)
                            console.log(...aditionalArray)
                            /* console.log(url)
                             console.log(images.concat(url))*/
                            console.log(`   ${i}`)
                            setImages([...images, ...aditionalArray])

                        })

            }
        }
    }

    return (
        <div className={s.container}>
            <div className={s.create__title}><h2> </h2></div>
            <div className={s.create__main}>
                <div className={s.create__item}>
                    <div className={s.subtitle}> </div>
                    <form className={s.create__form}>
                        <div className={s.field}>
                            <label> </label>
                            <input
                                type="text"
                                className={s.input__text}
                                onChange={onHandleUpdate}
                                id="address"
                                value={newItem.address}
                            />
                        </div>
                        <div className={s.field}>
                            <label> </label>
                            <Tooltips options={options}
                                      setSelected={setSelectedRooms}
                                      selected={selectedRooms}/>

                        </div>

                        <div className={s.field}>
                            <label> </label>
                            <div className={s.field__wrapper}>
                                <input
                                    type="text"
                                    className={s.input__text + " " + s.square}
                                    onChange={onHandleUpdate}
                                    id="square"
                                    value={newItem.square}
                                />
                                <span className={s.field__text}><sup>2</sup></span>
                            </div>
                        </div>
                        <div className={s.field}>
                            <label></label>
                            <input
                                type="text"
                                className={s.input__text + " " + s.floors}
                                onChange={onHandleUpdate}
                                id="floor"
                                value={newItem.floor}
                            />
                        </div>
                    </form>
                </div>
                <div className={s.create__item + " " + s.create__photos}>
                    <div className={s.subtitle}></div>
                    <div className={s.guide}>
                               ,   
                         . JPG, PNG  GIF.    10 
                    </div>
                    <div className={s.loader}>
                        {images
                            ?
                            images.map((img, index) =>
                                <div key={index} className={s.loader__photos}>
                                    <img src={img} alt="flat"/>
                                </div>)
                            : null}
                        <form className={s.loader__photos + " " + s.loader__upload}>
                            <FiImage/>
                            <p> <span>*</span></p>
                            <input
                                type="file"
                                multiple
                                className={s.input__file}
                                onChange={onHandleChangeFile}
                                id="Photos"
                            />
                        </form>
                    </div>
                    <div className={s.description}>
                        <div className={s.subtitle}></div>
                        <textarea
                            type="textarea"
                            placeholder="    , , ,   ;
							  ;        ;    ."
                            className={s.input__textarea}
                            onChange={onHandleUpdate}
                            id="description"
                            value={newItem.description}
                        />
                    </div>
                </div>
                <div className={s.create__item + " " + s.create__photos}>
                    <div className={s.subtitle}>  </div>
                    <form className={s.create__form}>
                        <div className={s.field}>
                            <label></label>
                            <div className={s.field__wrapper}>
                                <input
                                    type="text"
                                    className={s.input__text + " " + s.price}
                                    onChange={onHandleUpdate}
                                    value={newItem.price}
                                    id="price"
                                />
                                <span className={s.field__text}></span>
                            </div>
                        </div>


                        <div className={s.field}>
                            <label> </label>
                            <input
                                type="text"
                                className={s.input__text}
                                onChange={onHandleUpdate}
                                id="type__of__sale"
                            />
                        </div>
                        <div className={s.field}>
                            <label></label>
                            <input
                                type="text"
                                className={s.input__text + " " + s.phone}
                                onChange={onHandleUpdate}
                                id="phone"
                                value={newItem.phone}
                            />
                        </div>
                    </form>
                </div>
                <div className={s.create__item + " " + s.create__buttons}>
                    <button type="button"
                            className={s.create__button + " " + s.close__button}
                            onClick={closeEdit}></button>
                    <button type="button"
                            className={s.create__button + " " + (editCondition ? s.success__button : s.disabled__button)}
                            onClick={editCondition ? edit : null}>
                        
                    </button>
                </div>
            </div>
        </div>
    )
}



