import React, {useEffect, useState} from "react";
import api from "../../../dataAccessLayer/firebase";
import Chart from "react-apexcharts";
import {BrowserRouter as Router, Switch, NavLink, Route} from "react-router-dom";

// 
import './advView.scss';
import {Preloader} from "../../common/preloader/preloader";
import {Description} from "./Description";
import {Price} from "./price/Price";
import {Slider} from "./slider/slider";
import {Reviews} from "./reviews/Reviews";


export const AdvViewContainer = ({match}) => {
    const [current, setCurrent] = useState({});
    const [isLoading, setIsLoading] = useState(false);
    const [infoSection, setInfoSection] = useState("Where buy");
    const [priceHistory, setPriceHistory] = useState([]);
    const chartWidth = "350px"
    const chartHeight = "220px"
    const options = {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        timezone: 'UTC',
        hour: 'numeric',
        minute: 'numeric'
    };

    const fetchFlatById = async () => {
        if (!isLoading) {
            const res = await api.getAdvById(match.params.id)
            setCurrent(res)
            setIsLoading(true)
            let aditionalPriceHistory = res.priceHistory.map(item => {
                return ({x: item.date.toDate().getTime(), y: Number(item.price)})
            })
            setPriceHistory(aditionalPriceHistory)
        }
    }


    useEffect(() => {
        fetchFlatById()
    }, [])


    const chartState = {
        options: {
            chart: {
                id: "basic-bar"
            },
            stroke: {
                curve: 'smooth',
            },
            xaxis: {
                type: 'datetime'
            }
        },
        series: [
            {
                name: "",
                data: priceHistory
            }
        ]
    };

    return (!isLoading
            ? <Preloader/>
            :
            <Router>
                <div className="flats">
                    <div className="container">
                        <div className="item">
                            <div className="item__main">
                                <div className="breadcrumbs">
                                    <NavLink to='/'>
                                           /
                                    </NavLink>
                                    {`${current.rooms} .  ${current.square} 2`}
                                </div>
                                <div className="item__date">
                                    {`${current.date.toDate().toLocaleString("ru", options)}`}
                                </div>

                                <div className="item__card">
                                    <div className="item__title">
                                        {`${current.rooms} .  ${current.square} 2`}
                                    </div>
                                    <div className="item__tabs">
                                        <NavLink to={`${match.url}`}>
                                            <div
                                                className={"item__tab" + " " + (infoSection === "Where buy" && "item__tab-active")}
                                                onClick={() => setInfoSection("Where buy")}> 
                                            </div>
                                        </NavLink>
                                        <NavLink to={`${match.url}/description`}>
                                        <div
                                            className={"item__tab" + " " + (infoSection === "Discription" && "item__tab-active")}
                                            onClick={() => setInfoSection("Discription")}>
                                        </div>
                                        </NavLink>
                                        <NavLink to={`${match.url}/reviews`}>
                                            <div
                                                className={"item__tab" + " " + (infoSection === "Reviews" && "item__tab-active")}
                                                onClick={() => setInfoSection("Reviews")}>
                                            </div>
                                        </NavLink>
                                        <NavLink to={`${match.url}/discussion`}>
                                            <div
                                                className={"item__tab" + " " + (infoSection === "Discussion" && "item__tab-active")}
                                                onClick={() => setInfoSection("Discussion")}>
                                            </div>
                                        </NavLink>
                                        <NavLink to={`${match.url}/useful`}>
                                            <div
                                                className={"item__tab" + " " + (infoSection === "Useful" && "item__tab-active")}
                                                onClick={() => setInfoSection("Useful")}>
                                            </div>
                                        </NavLink>
                                    </div>

                                    <div className="item__underline"></div>

                                    <div className="item__content">
                                        <div className="item__slider">
                                            <Slider imgUrl={current.imgUrl} name={current.address}/>
                                        </div>
                                        <div className="item__price">
                                            <div className="item__digital"> {`${current.price} `} </div>
                                            <div className="item__chart">
                                                <Chart
                                                    options={chartState.options}
                                                    series={chartState.series}
                                                    type="line"
                                                    width={chartWidth}
                                                    height={chartHeight}

                                                />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="item__info">
                                        <h2>{`${current.rooms} .  ${current.square} 2`}</h2>

                                        <Switch>
                                            <Route
                                                exact path={`${match.path}`}
                                                render={props => <Price {...props} current={current}/>}
                                            />
                                            <Route exact path={`${match.path}/description`}
                                                   render={props => <Description {...props} current={current}/>}
                                            />
                                            <Route
                                                exact path={`${match.path}/reviews`}
                                                render={props => <Reviews {...props} current={current}/>}
                                            />
                                            <Route
                                                exact path={`${match.path}/discussion`}
                                                render={props => <Reviews {...props} current={current}/>}
                                            />
                                            <Route
                                                exact path={`${match.path}/usefull`}
                                                render={props => <Reviews {...props} current={current}/>}
                                            />

                                        </Switch>

                                    </div>
                                </div>

                            </div>
                            <div className="item__sidepanel">
                                  
                            </div>
                        </div>
                    </div>
                </div>
            </Router>
    )
}
