import React, {useState} from 'react';
import {FiImage} from 'react-icons/fi';
import {Link} from "react-router-dom";
import s from './Create.module.scss'
import {storage} from "../../../dataAccessLayer/fbConfig";
import api from "../../../dataAccessLayer/firebase";
import {useSelector} from "react-redux"
import {Button} from "@material-ui/core";
import {Tooltips} from "../../common/Tooltips/Tooltips";

export const Create = ({history}) => {
    const userId = useSelector(state => state.auth.userId)
    const isAuth = useSelector(state => state.auth.isAuth)
    const [advInfo, setAdvInfo] = useState([])
    const [imgUrl, setImgUrl] = useState([]);
    const [progressBar, setProgressBar] = useState(0)
    const options = ['1', '2', '3', '4', '5', '6  ']
    const [selectedRooms, setSelectedRooms] = useState(null)



    let createCondition =
        progressBar === 100 &&
        advInfo.address !== '' &&
        advInfo.price !== '' &&
        advInfo.phone !== '' &&
        advInfo.rooms !== '' &&
        advInfo.square !== '' &&
        advInfo.floor !== '' &&
        advInfo.description

    const makeDraft = () => {
        setImgUrl([]);
        setProgressBar(0);
		history.replace('/flat')
    }
    const addFlat = () => {
        const date = new Date()
        const priceHistory = [{date: date, price: advInfo.price}]
        api.addAdv(
            imgUrl,
            advInfo.address,
            advInfo.price,
            priceHistory,
            advInfo.phone,
            selectedRooms,
            advInfo.square,
            advInfo.floor,
            advInfo.description,
            date,
            userId)
        setProgressBar(0);
        setImgUrl([]);
        history.replace('/flat')
    }
    //    input'ov
    const onHandleCreateUserForm = e => {
        setAdvInfo({...advInfo, [e.target.id]: e.target.value})
    }


//    Firebase
    const onHandleChangeFile = async e => {
        const files = e.target.files
        if (files !== []) {
            let aditionalArray = [];
            console.log(`  ${files.length}`)
            for (let i = 0; i < files.length; i++) {
                const file = files[i]
                if (!file.type.match('image.*')) {
                    continue;
                }
                storage.ref(`/images/${file.name}`).put(file)
                    .on('state_changed',
                        async snapshot => {
                            await setProgressBar(Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100));
                            console.log('  ' + progressBar)
                        },
                        error => {
                            console.log(error);
                        },
                        async () => {
                            const url = await storage.ref('images').child(file.name).getDownloadURL()
                            console.log(imgUrl)
                            console.log(url)
                            aditionalArray.push(url)
                            console.log(imgUrl.concat(url))
                            setImgUrl([...imgUrl, ...aditionalArray])
                            console.log(imgUrl)
                        })
                console.log(`   ${i}`)

            }
        }
    }

    return (
        <div className={s.container}>
            <div className={s.create__title}><h2> </h2></div>
            <div className={s.create__main}>
                <div className={s.create__item}>
                    <div className={s.subtitle}> </div>
                    <form className={s.create__form}>
                        <div className={s.field}>
                            <label> </label>
                            <input
                                type="text"
                                className={s.input__text}
                                onChange={onHandleCreateUserForm}
                                id="address"
                            />
                        </div>
                        <div className={s.field}>
                            <label> </label>
                            <Tooltips options={options}
                                      setSelected={setSelectedRooms}
                                      selected={selectedRooms}/>
                        </div>

                        <div className={s.field}>
                            <label> </label>
                            <div className={s.field__wrapper}>
                                <input
                                    type="text"
                                    className={s.input__text + " " + s.square}
                                    onChange={onHandleCreateUserForm}
                                    id="square"
                                />
                                <span className={s.field__text}><sup>2</sup></span>
                            </div>
                        </div>
                        <div className={s.field}>
                            <label></label>
                            <input
                                type="text"
                                className={s.input__text + " " + s.floors}
                                onChange={onHandleCreateUserForm}
                                id="floor"
                            />
                        </div>
                    </form>
                </div>
                <div className={s.create__item + " " + s.create__photos}>
                    <div className={s.subtitle}></div>
                    <div className={s.guide}>
                               ,   
                         . JPG, PNG  GIF.    10 
                    </div>
                    <div className={s.loader}>
                        {imgUrl
                            ?
                            imgUrl.map(img =>
                                <div className={s.loader__photos}>
                                    <img src={img} alt="flat"/>
                                </div>)
                            : null}
                        <form className={s.loader__photos + " " + s.loader__upload}>
                            <FiImage/>
                            <p> <span>*</span></p>
                            <input
                                type="file"
                                multiple
                                className={s.input__file}
                                onChange={onHandleChangeFile}
                                id="Photos"
                            />
                        </form>
                    </div>
                    <div className={s.description}>
                        <div className={s.subtitle}></div>
                        <textarea
                            type="textarea"
                            placeholder="    , , ,   ;
							  ;        ;    ."
                            className={s.input__textarea}
                            onChange={onHandleCreateUserForm}
                            id="description"
                        />
                    </div>
                </div>
                <div className={s.create__item + " " + s.create__photos}>
                    <div className={s.subtitle}>  </div>
                    <form className={s.create__form}>
                        <div className={s.field}>
                            <label></label>
                            <div className={s.field__wrapper}>
                                <input
                                    type="text"
                                    className={s.input__text + " " + s.price}
                                    onChange={onHandleCreateUserForm}
                                    id="price"
                                />
                                <span className={s.field__text}></span>
                            </div>
                        </div>

                        <div className={s.field}>
                            <label> </label>
                            <input
                                type="text"
                                className={s.input__text}
                                onChange={onHandleCreateUserForm}
                                id="type__of__sale"
                            />
                        </div>
                        <div className={s.field}>
                            <label></label>
                            <input
                                type="text"
                                className={s.input__text + " " + s.phone}
                                onChange={onHandleCreateUserForm}
                                id="phone"
                            />
                        </div>

                    </form>
                </div>
                <div className={s.create__item}>
                    {isAuth
                        ?
                        <div className={s.create__buttons}>
							<Button
								variant="contained"
								color="secondary"
								type="button"
								className={s.create__button + " " + s.close__button}
								onClick={makeDraft}>
								 
							</Button>

							<button type="button"
									className={s.create__button + " " + (createCondition ? s.success__button : s.disabled__button)}
									onClick={createCondition ? addFlat : null}
							>
							</button>
                        </div>
                        :
						<div className={s.auth}>
							<div className={s.auth__title}>  </div>
							<div className={s.auth__widget}>
								<div className={s.auth__row}>
									<form>
									<div className={s.auth__content}>
										<input type="text"
											   placeholder="Email  id"
											   className={s.auth__input}/>
									</div>
									<button type="button" className={s.auth__button}>
										<span className={s.auth__authentication}></span>
									</button>
								</form>
								</div>
								<div className={s.auth__container}>
									<Link className={s.auth__link}to='/contacts'> ?</Link>
								</div>
							</div>
						</div>
                    }
                </div>
            </div>
        </div>
    )
}
