import api from '../dataAccessLayer/firebase'

let initialState = {
    userId: null,
    email: null,
    name: null,
    isFetching: false,
    isAuth: false
};

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SET_AUTH_STATUS':
            return {
                ...state,
                isAuth: action.status
            }
        case 'SET_USER_NAME':
            return {
                ...state,
                name: action.name
            }
        case 'SET_USER_ID':
            return {
                ...state,
                userId: action.userId
            }
        default:
            return state;
    }
}

export const setAuthStatusAC = status => ({type: 'SET_AUTH_STATUS', status})
export const setUserNameAC = name => ({type: 'SET_USER_NAME', name})
export const setUserIdAC = userId => ({type: 'SET_USER_ID', userId})





export const reg = (name, email, password) => dispatch => {
    api.register(name, email, password)
        .then(data => {
            dispatch(setAuthStatusAC(true))
            dispatch(setUserNameAC(data.user.displayName))
            dispatch(setUserIdAC(data.user.uid))
    })
}

export const login = (email, password) => dispatch => {
    api.login(email, password)
        .then(data => {
            dispatch(setAuthStatusAC(true))
            dispatch(setUserNameAC(data.user.displayName))
            dispatch(setUserIdAC(data.user.uid))
    })
}


export const logout = () => dispatch => {
    api.logout().then(() => {
        dispatch(setAuthStatusAC(false))
        dispatch(setUserIdAC(null))
        dispatch(setUserNameAC(null))
    })
}

