import React, {Fragment, useState} from "react";
import s from './Header.module.scss';
import {Link, withRouter} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {logout} from "../../../store/auth-reducer";
import {Button} from "@material-ui/core";
import {FiHeart, FiMessageCircle, FiUsers} from "react-icons/fi";


export const Header = withRouter(({history}) => {
    const isAuth = useSelector(state => state.auth.isAuth)
    const name = useSelector(state => state.auth.name)
    const dispatch = useDispatch()
    const [iconStatus, setIconStatus] = useState(false)

    const logoutHandler = async () => {
        try {
            await dispatch(logout())
            history.replace('/')
        } catch (error) {
            alert(error.message)
        }
    }

    return (
        <Fragment>
            <header className={s.header}>
                <div className={s.container}>
                    <div className={s.header__wrapper}>
                        <Link to='/'><img src='https://vk.com/images/svg_icons/ic_head_logo.svg'/></Link>
                        <input className={s.header__search} type="text" placeholder=""/>

                        {isAuth
                            ?
                            <div className={s.header__right}>
                                <Link to='/my'>
                                    <div className={s.header__item}>{`${name}`}</div>
                                </Link>
                                <Link to='/'>
                                    <div className={s.header__item} onClick={logoutHandler}></div>
                                </Link>
                            </div>

                            : <div className={s.header__item}>
                                <Link to={'/login'}><FiUsers/>   </Link>
                            </div>
                        }
                        <div className={s.nav__mobile}>
                            <div className={s.burger_icon} onClick={() => setIconStatus(!iconStatus)}>
                                <i className={iconStatus ? s.open : s.close}></i>
                                <i className={iconStatus ? s.open : s.close}></i>
                                <i className={iconStatus ? s.open : s.close}></i>
                            </div>
                        </div>
                    </div>
                </div>
            </header>
            <nav className={s.menu}>
                <div className={s.container}>
                    <div className={s.menu__wrapper}>
                        <ul className={s.menu__main}>
                            <li className={s.menu__link}>
                                <Link to='/flat'></Link>
                            </li>
                            <li className={s.menu__link}></li>
                            <li className={s.menu__link}></li>
                            <li className={s.menu__link}></li>
                        </ul>

                        <div className={s.menu__items}>
                            {isAuth
                                ?
                                <div className={s.menu__item}>
                                    <div className={s.menu__icon}>
                                        <Link to='/favorites'><span><FiHeart/></span></Link>
                                    </div>
                                    < div className={s.menu__icon}>
                                        <Link to='/my/dialogs'><span><FiMessageCircle/></span></Link>
                                    </div>
                                </div>
                                :
                                null
                            }

                            <div className={s.menu__item + " " + s.menu__button}>
                                <Link to={'/create'}>
                                    <Button
                                        type="submit"
                                        fullWidth
                                        variant="contained"
                                        color="secondary"
                                    > 
                                    </Button>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
        </Fragment>
    )
})

